.cursor {
  width: 30px;
  height: 30px;
  pointer-events: none;
  z-index: 1000000000;
  border: 1px solid #000;
  border-radius: 100%;
  transition: all .2s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(calc(15px - 50%), -50%);
}

.cursor2 {
  width: 15px;
  height: 15px;
  opacity: .3;
  pointer-events: none;
  z-index: 1000000000;
  background-color: #fff;
  border-radius: 100%;
  transition: width .3s, height .3s, opacity .3s;
  position: fixed;
  transform: translate(-50%, -50%);
}

.hover {
  opacity: .5;
  background-color: #f3c653;
}

.cursorinnerhover {
  width: 50px;
  height: 50px;
  opacity: .5;
}

.header-indicator {
